<template>
  <div class="ratings">
    <div class="ratings-content">
      <div class="overview">
        <div class="overview-left">
          <h1 class="score">{{seller.score}}</h1>
          <div class="title"></div>
          <div class="rank">{{seller.rankRate}}%</div>
        </div>
        <div class="overview-right">
          <div class="score-wrapper">
            <span class="title"></span>
            <star :size="36" :score="seller.serviceScore"></star>
            <span class="score">{{seller.serviceScore}}</span>
          </div>
          <div class="score-wrapper">
            <span class="title"></span>
            <star :size="36" :score="seller.foodScore"></star>
            <span class="score">{{seller.foodScore}}</span>
          </div>
          <div class="delivery-wrapper">
            <span class="title"></span>
            <span class="deliveryTime">{{seller.deliveryTime}}</span>
          </div>
        </div>
      </div>
      <split></split>
      <ratingselect @ratingtype-select="ratingtypeSelect"
                    @onlyContent-toggle="onlyContentToggle"
                    :selectType="selectType"
                    :onlyContent="onlyContent"
                    :ratings="ratings"
                    :desc="desc"></ratingselect>
      <div class="rating-wrapper">
        <ul>
          <li v-for="rating in ratings" v-show="needShow(rating.rateType,rating.text)" class="rating-item">
            <div class="avatar">
              <img :src="rating.avatar" width="28px" height="28px;">
            </div>
            <div class="content">
              <h1 class="name">{{rating.username}}</h1>
              <div class="star-wrapper">
                <star :size=24 :score="rating.score"></star>
                <span class="delivery" v-show="rating.deliveryTime">{{rating.deliveryTime}}</span>
              </div>
              <p class="text">{{rating.text}}</p>
              <div class="recommend" v-show="rating.recommend && rating.recommend.length">
                <span class="icon-thumb_up"></span>
                <span class="item" v-for="item in rating.recommend">{{item}}</span>
              </div>
              <div class="time">{{formateDate(rating.rateTime)}}</div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import BScroll from 'better-scroll';
  import {formateDate} from '@/common/js/formateDate';
  import star from '@/components/star/star';
  import split from '@/components/split/split';
  import ratingselect from '@/components/ratingselect/ratingSelect';

  // const POSITY = 0;
  // const NEGATIVE = 1;
  const ALL = 2;
  const ERR_OK = 0;

  export default{
    name: 'ratings',
    props: {
      seller: {
        type: Object
      }
    },
    created () {
      this.$http({
        url: '/ratings',
        success (data) {
          if (data.errno === ERR_OK) {
            this.ratings = data.data;
            this.$nextTick(() => {
              this.scroll = new BScroll(this.$el, {
                click: true
              });
            });
          }
        },
        ctx: this
      });
    },
    data () {
      return {
        ratings: [],
        showFlag: false,
        selectType: ALL,
        onlyContent: false,
        desc: {
          all: '',
          positive: '',
          negative: ''
        }
      };
    },
    methods: {
      ratingtypeSelect (type) {
        this.selectType = type;
        this.$nextTick(() => {
          this.scroll.refresh();
        });
      },
      onlyContentToggle (onlyContent) {
        this.onlyContent = onlyContent;
        this.$nextTick(() => {
          this.scroll.refresh();
        });
      },
      formateDate (time) {
        let date = new Date(time);
        return formateDate(date, 'yyyy-MM-dd hh:mm');
      },
      needShow (type, text) {
        if (this.onlyContent && !text) {
          return false;
        }
        if (this.selectType === ALL) {
          return true;
        } else {
          return type === this.selectType;
        }
      }
    },
    components: {
      star,
      split,
      ratingselect
    }
  };
</script>

<style lang="scss" scoped>
  @import "../../common/styles/icon-style";
  @import "../../common/styles/mixin";

  .ratings {
    position: absolute;
    top: 174px;
    bottom: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    .overview {
      display: flex;
      padding: 18px 0;
      .overview-left {
        flex: 0 0 137px;
        width: 137px;
        border-right: 1px solid rgba(7, 17, 27, 0.1);
        text-align: center;
        padding-bottom: 6px;
        @media only screen and (max-width: 320px) {
          flex: 0 0 120px;
          width: 120px;
        }
        .score {
          line-height: 28px;
          font-size: 24px;
          color: rgb(255, 153, 0);
          margin-bottom: 6px;
        }
        .title {
          line-height: 12px;
          font-size: 12px;
          color: rgb(7, 17, 27);
          margin-bottom: 8px;
        }
        .rank {
          line-height: 10px;
          font-size: 10px;
          color: rgb(147, 153, 159);
        }
      }
      .overview-right {
        flex: 1;
        padding: 0px 0 6px 24px;
        @media only screen and (max-width: 320px) {
          padding-left: 6px;
        }
        .score-wrapper {
          margin-bottom: 8px;
          font-size: 0;
          .title {
            display: inline-block;
            vertical-align: top;
            font-size: 12px;
            line-height: 18px;
            color: rgb(7, 17, 27);
          }
          .star {
            display: inline-block;
            vertical-align: top;
            margin: 0 12px;
          }
          .score {
            display: inline-block;
            line-height: 18px;
            vertical-align: top;
            font-size: 12px;
            color: rgb(255, 153, 0);
          }
        }
        .delivery-wrapper {
          font-size: 0;
          .title {
            font-size: 12px;
            line-height: 12px;
            color: rgb(7, 17, 27);
          }
          .deliveryTime {
            margin-left: 12px;
            font-size: 12px;
            color: rgb(147, 153, 159);
          }
        }
      }
    }
    .rating-wrapper {
      padding: 0 18px;
      .rating-item {
        display: flex;
        padding: 18px 0;
        @include border-1px(rgba(7, 17, 27, 0.1));
        .avatar {
          flex: 0 0 28px;
          width: 28px;
          margin-right: 12px;
          img {
            border-radius: 50%;
          }
        }
        .content {
          flex: 1;
          position: relative;
          .name {
            margin-bottom: 4px;
            line-height: 12px;
            font-size: 10px;
            color: rgb(7, 17, 27);
          }
          .star-wrapper {
            margin-bottom: 6px;
            font-size: 0;
            .star {
              display: inline-block;
              margin-right: 6px;
              vertical-align: top;
            }
            .delivery {
              display: inline-block;
              margin-right: 6px;
              vertical-align: top;
              line-height: 12px;
              font-size: 10px;
              color: rgb(147,153,159);
            }
          }
        }
      }
    }
    .text {
      line-height: 18px;
      margin-bottom: 8px;
      color: rgb(7, 17, 27);
      font-size: 12px;
    }
    .recommend {
      line-height: 16px;
      font-size: 0;
      .icon-thumb_up, .item {
        display: inline-block;
        margin: 0 8px 4px 0;
        font-size: 9px;
      }
      .icon-thumb_up {
        color: rgb(0, 160, 220);
      }
      .item {
        padding: 0 6px;
        border: 1px solid rgba(7, 17, 27, 0.1);
        border-radius: 1px;
        color: rgb(147, 153, 159);
        background-color: #fff;
      }
    }
    .time{
      position: absolute;
      top: 0;
      right: 0;
      line-height: 12px;
      font-size: 10px;
      color: rgb(147, 153, 159);
    }
  }
</style>



